.dataTable {
    padding: 20px;
    width: 100%;
    height: calc(100vh - 90px);

    .datatableTitle {
        display: flex;
        align-items: center;
        justify-content: space-between;

        h1 {
            font-size: 20px;
            color: lightgray;
        }

        .link {
            padding: 5px;
            border: 0.5px solid green;
            border-radius: 5px;
            text-decoration: none;
            color: green;
            margin-bottom: 10px;
        }
    }
    .cellWithImg {
        display: flex;
        align-items: center;
        justify-content: center;

        .cellImg {
            width: 30px;
            height: 30px;
            border-radius: 50%;
            object-fit: cover;
            margin-right: 20px;
        }
    }

    .cellWithStatus {
            padding: 5px;
            border-radius: 4px;

            &.active {
                color: green;
                background: rgba(0, 128, 0, 0.151);
            }

            &.passive {
                color: goldenrod;
                background: rgba(189, 189, 3, 0.103);
            }
            &.pending {
                color: red;
                 background: rgba(31, 1, 1, 0.103);
            }
    }

    .actionCell {
        display: flex;
        align-items: center;
        gap: 10px;

        .viewButton {
            padding: 3px 7px;
            border: 1px dotted rgba(0, 0, 255, 0.438);
            cursor: pointer;

            &:hover {
                background: #1818184d;
                color: white;
            }
        }

        .deleteButton {
            padding: 3px 7px;
            border: 1px dotted rgba(220, 20, 60, 0.411);
            color: rgba(220, 20, 60, 0.9);
            cursor: pointer;

            &:hover {
                background: #1818184d;
                color: white;
            }
        }
    }
}