.side-bar {
    flex: 1;
    border-right: 0.5px solid rgb(168, 166, 166);
    min-height: 100vh;

    .top {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px;

        .logo {
            font-size: 24px;
            font-weight: 700;
            color:rgb(43, 88, 184)
        }
    }
    
    hr {
        border: 0.5px solid rgb(168, 166, 166);
    }

    .center {
        padding: 4px 10px;

        ul {
            list-style: none;
            
            .title {
                font-size: 11;
                font-weight: bold;
                color: #999;
                margin-top: 15px;
                margin-top: 10px;
            }
            li {
                display: flex;
                align-items: center;
                padding: 5px;
                cursor: pointer;

                &:hover {
                    background-color: #ece8ff;
                }

                .icon {
                    font-size: 18px;
                    color: #7451f8
                }

                span {
                    font-size: 13px;
                    margin-left: 10px;
                    font-weight: 600;
                }
            }
        }
    }
}

@media (max-width: 500px) {
    .sideBar{
        flex: 0.5;
    }
}