.widget {
    flex: 1;
    display: flex;
    justify-content: space-between;
   -webkit-box-shadow: 0px 2px 6px -3px rgba(0,0,0,0.70); 
box-shadow: 0px 2px 6px -3px rgba(0,0,0,0.70);
    height: 100px;
    border-radius: 10px;
    padding: 6px;

    .left, .right{
        display: flex;
        flex-direction: column;
        justify-content: space-between;


        .title{
            font-weight: bold;
            font-size: 15px;
            color: rgb(138, 135, 135);
        }

        .counter {
            font-weight: 300;
            font-size: 30px;
        }

        .link {
            font-size: 12px;
            border-bottom: 1px solid gray;
            width: max-content;
            }

        .percentage {
            display: flex;
            align-items: center;
            font-size: 14px;

            &.positive {
                color: green;
            }
            &.negative {
                color: red;
            }
        }

        .icon {
           font-size: 24px;
           padding: 5px;

           background: rgb(134, 92, 201); 
           border-radius: 4px;
           align-self: flex-end;
        }
        span{
            flex: 1;
        }
    }
    
}