.app.dark {
    background: #000;
    color: rgb(156, 156, 156);

    .chartGrid {
        stroke: rgba(228, 228, 228, 0.15)
    }

    .table {
        background: #222;

        .tableCell {
            color: gray;
        }
    }

    .navBar {
        color: #999;
        border-color: #333;

        .search {
            border-color: gray;
        }
    }

    .side-bar {
        background: #000;
        border-color: #333;

        .top {
            .logo {
                color: #333;
            }
        }

        hr {
            border-color: #333;
        }

        ul {
            li {
                &:hover {
                    background: #333;
                }

                .icon {
                    color: #999;
                }
            }
        }
    }

    .dataTable {

        .dataGrid {
            color: gray;
            border: none;

            .viewButton, .cellWithStatus, .deleteButton {
                color: gray;
                border: none;
            }
        }
    }

    input {
        background: transparent;
    }

    .single {
        .singleContainer {
            .top {
                .left {
                    -webkit-box-shadow: 5px 5px 15px -4px rgba(221,221,221,0.45); 
                    box-shadow: 5px 5px 15px -4px rgba(221,221,221,0.45);
                }
                .right {
                    -webkit-box-shadow: 3px 4px 15px -4px rgba(221,221,221,0.54); 
                    box-shadow: 3px 4px 15px -4px rgba(221,221,221,0.54);
                }
            }
        }
    }

    .widget{
        -webkit-box-shadow: 3px 4px 15px -4px rgba(221,221,221,0.54); 
        box-shadow: 3px 4px 15px -4px rgba(221,221,221,0.54);
    }
    .charts {
        -webkit-box-shadow: 3px 4px 15px -4px rgba(221,221,221,0.54); 
        box-shadow: 3px 4px 15px -4px rgba(221,221,221,0.54);
    }
    .listContainer {
        -webkit-box-shadow: 3px 4px 15px -4px rgba(221,221,221,0.54); 
        box-shadow: 3px 4px 15px -4px rgba(221,221,221,0.54);
    }
    input{
        color: gray
    }
}
