.table {
    .cellWrapper {
        display: flex;
        align-items: center;
        height: 15px;
        width:156px;
        border-radius: 50%;
        margin-right: 4px;
        object-fit: cover;
    }
    
    .tableCell {

        .status {
            padding: 5px;
            border-radius: 4px;

            &.Approved {
                color: green;
                background: rgba(0, 128, 0, 0.151);
            }

            &.Pending {
                color: goldenrod;
                background: rgba(189, 189, 3, 0.103);
            }

        }
    }
}