.home {
    display: flex;

    .homeWrapper {
        flex: 6;

        main {
            

            .mainWrapper {
                padding: 20px;
                
                .widgets, .charts {
                    display: flex;
                    gap: 20px;
                    justify-content: space-around;
                }
                .charts {
                   padding: 8px 0;
                }
                .listContainer {
                    -webkit-box-shadow: 0px 2px 6px -3px rgba(0,0,0,0.70); 
                    box-shadow: 0px 2px 6px -3px rgba(0,0,0,0.70);
                    padding: 20px;
                    margin: 4px 20px;

                    .listTitle {
                        font-weight: 500;
                        color: gray;
                        margin-bottom: 10px;
                    }
                }
            }
        }
    }
}

@media (max-width: 768px) {
    .homeWrapper{
        width: 100vw;
    }

    .side-bar {
        display: none;
    }

    .widgets{
        flex-wrap: wrap;
        .widget{
            flex: 50%;
            min-width: 150px;
        }
    }

    .charts{
        flex-direction: column;
    }
}

@media (min-width: 768px) and (max-width: 1021 ) {
    .homeWrapper{
        background: yellow
    }

     .widgets{
        flex-wrap: wrap;
        .widget{
            flex: 25%;
        }
    }

    .charts{
        flex-direction: column;
    }

}