.single {
    display: flex;

    .singleContainer {
        flex: 6;

        .top {
            display: flex;
            padding: 20px;
            gap: 20px;

            .left {
                flex: 1;
                -webkit-box-shadow: 0px 2px 6px -3px rgba(0,0,0,0.70); 
                box-shadow: 0px 2px 6px -3px rgba(0,0,0,0.70);
                position: relative;
                padding: 20px;

                .editButton {
                    position: absolute;
                    top: 0;
                    right: 0;
                    padding: 2px 5px;
                    color: purple;
                    background: #7551f828;
                    border-radius: 0 5px 0 0;
                    cursor: pointer;
                }

                .item {
                    display: flex;

                    .itemImg {
                        width: 100px;
                        height: 100px;
                        border-radius: 50%;
                        object-fit: cover;
                        margin-right: 20px;
                    }

                    .details {
                        .itemTitle {
                            margin-bottom: 15px;
                            color: #555;
                        }
                        
                        .detailItem {
                            margin-bottom: 10px;
                            font-size: 14px;
                            display: flex;
                            gap: 5px;

                            .itemKey {
                                font-weight: 600;
                                color: gray
                            }
                        }
                    }
                }
            }

            .right {
                flex: 2;
                 -webkit-box-shadow: 0px 2px 6px -3px rgba(0,0,0,0.70); 
                    box-shadow: 0px 2px 6px -3px rgba(0,0,0,0.70);
            }
        }

        .bottom {
            padding: 20px;
        }
    }

    .title {
        font-size: 16px;;
        color: lightgray;
        margin-bottom: 20px;
    }
}