nav {
    height: 50px;
    width: 100%;
    border-bottom: 0.5px solid rgb(168, 166, 166);
    color: #555;
    display: flex;
    top: 0;
    align-items: center;
 
    .navbar-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        align-items: center;
        padding: 4px 20px;
        width: 100%;

        .search {
            display: flex;
            align-items: center;
            border: 0.5px solid lightgray;
            padding: 4px;

            input {
                border: none;
                outline: none;
                background: transparent;
                color: gray;

                &::placeholder {
                    font-size: 12px;
                }
            }
        }

        .items {
            display: flex;
            align-items: center;
            justify-content: space-around;

            .item {
                display: flex;
                align-items: center;
                font-size: px;
                cursor: pointer;
                position: relative;
                margin-right: 10px;

                .icon {
                    font-size: 25px;
                }

                .counter {
                    width: 15px;
                    height: 15px;
                    border-radius: 50%;
                    background: red;
                    color: white;
                    font-size: 10px;
                    position: absolute;
                    top: -3px;
                    right: -2px;
                     display: flex;
                    justify-content: center;
                    align-items: center;
                }

                .avatar {
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                    object-fit: cover;
                }
            }
        }

    
    }
}

@media (max-width: 500px) {
    nav{
        height: 100px;

        .navbar-wrapper{
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 12px;
        }
    }
}

