.featured {
    flex: 2;
    justify-content: space-between;
    -webkit-box-shadow: 0px 2px 6px -3px rgba(0,0,0,0.70); 
box-shadow: 0px 2px 6px -3px rgba(0,0,0,0.70);
    border-radius: 10px;
    padding: 8px 10px;

    .top {
        display: flex;
        justify-content: space-between;
        width: 100%;

        h1 {
            font-size: 18px;
        }
    }

    .bottom {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        gap: 10px;
        padding: 20px;

        .featuredChart {
            width: 100px;
            height: 100px;
        }

        .title {
            font-weight: 500;
            color: gray;
        }

        .amount {
            font-size: 30px;
        }

        .desc {
            font-weight: 500;
            font-size: 13px;
            color: gray;
            text-align: center;
        }

        .summary {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .item {
                text-align: center;

                .itemTitle {
                    font-size: 15px;
                    color: gray;
                }

                .itemResult {
                    display: flex;
                    align-items: center;
                    margin-top: 10px;
                    font-size: 13px;

                    &.positive {
                        color: green
                    }

                    &.negative {
                        color: red
                    }
                }
            }
        }
    }
}