.new {
    display: flex;
    width: 100%;

    .newContainer {
        flex: 6;
        width: 100%;
        
        .wrapper {
            display: flex;
            flex-direction: column;
            padding: 20px;
            gap: 20px;

            .top, .bottom {
                display: flex;
                padding: 20px;
                -webkit-box-shadow: 0px 2px 6px -3px rgba(0,0,0,0.70); 
                box-shadow: 0px 2px 6px -3px rgba(0,0,0,0.70);
                width: 100%;

                .title {
                    color: lightgray;
                    font-size: 20px;
                }

                .left {
                    flex: 1;

                    img {
                        width: 100px;
                        height: 100px;
                        border-radius: 50%;
                        object-fit: cover;
                    }
                }

                .right {
                    flex: 2;

                    form {
                        display: flex;
                        flex-wrap: wrap;
                        justify-content: space-around;
                        gap: 20px;

                        .formInput {
                            width: 40%;

                            label {
                                display: flex;
                                align-items: center;
                                gap: 10px;
                                font-weight: 600;

                                .icon {
                                    cursor: pointer;
                                }
                            }

                            input{
                                width: 100%;
                                height: 40px;
                                padding: 7px;
                                border: none;
                                outline: none;
                                border-bottom: 1px solid gray;
                            }
                        }

                        button {
                                width: 150px;
                                padding: 10px;
                                background: teal;
                                color: white;
                                font-weight: bold;
                                border: none;
                                margin-top: 10px;
                                cursor: pointer
                        }
                    }
                }
            }
        }
        
    }
}

@media (max-width: 500px){
    .new {
        flex: 10;
        .newContainer {
            .wrapper{
                padding: 0px;

                .bottom {
                    flex-direction: column;

                    .right{
                        form{
                            flex-direction: column;

                            .formInput{
                                width: 100%;

                                input {
                                    width: 100%;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    
}